.MainMenu {
  display: none;
  position: absolute;
  justify-content: space-between;
  flex-direction: column;
  width: 90%;
  left: 5%;
  top: 10%;
  z-index: 11;
  padding: 20px;
  border-radius: 20px; }
  .MainMenu .octave button {
    margin: 3px 3px;
    width: 40px; }
  .MainMenu button,
  .MainMenu p {
    display: inline-block; }
  .MainMenu .div {
    flex: 1;
    text-align: center;
    margin-top: 10px; }
  .MainMenu .closeMenu {
    width: 40px;
    align-self: flex-end; }
  .MainMenu.open {
    display: flex;
    background-color: beige; }
  .MainMenuDot {
    /*Bounce In Effect from Hover.css https://ianlunn.github.io/Hover/*/
    display: inline-block;
    vertical-align: middle;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.5s;
    transition-duration: 0.5s;
    /*mine */
    position: absolute;
    top: 35%;
    right: 5%;
    height: 50px;
    width: 50px;
    background-color: #129614;
    border-radius: 50%;
    z-index: 10;
    text-align: center;
    /* taken from https://ianlunn.github.io/Hover/ */ }
    .MainMenuDot span {
      font-size: 34px;
      color: yellow; }
    .MainMenuDot:hover, .MainMenuDot:focus, .MainMenuDot:active {
      -webkit-transform: scale(1.2);
      transform: scale(1.2);
      -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
      transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36); }

.Menu-Row {
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.Menu-label {
  flex: 1; }

.modalCover {
  background-color: rgba(0, 0, 0, 0.6);
  z-index: 10;
  position: absolute;
  width: 100%;
  height: 100%;
  display: none; }
  .modalCover.open {
    display: block; }

